.application {
  &__wrapper {
    position: relative;
    width: 100%;
    padding: 40px 0 0;
    @include screen(tablet) {
      padding: 60px 0 0;
    }
    @include screen(desktop) {
      padding: 80px 0 0;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(1, minmax(0, 1fr));
    gap: 32px;
    @include screen(tablet) {
      gap: 0;
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }
  }
  &__content {
    @include screen(tablet) {
      padding: 50px 0 20px;
    }
    @include screen(laptop) {
      padding: 110px 0 80px;
    }
  }
  &__title {
    margin-bottom: 16px;
    @include screen(tablet) {
      margin-bottom: 0;
      text-align: left;
    }
    @include screen(tablet) {
      font-size: 40px;
      line-height: 48px;
    }
  }
  &__text {
    font-weight: 500;
    color: #6E6E6E;
    margin-top: 16px;
    text-align: center;
    font-size: 16px;
    line-height: 19px;
    @include screen(tablet) {
      margin-top: 32px;
      text-align: left;
    }
    @include screen(laptop) {
      margin-top: 50px;
      font-size: 24px;
      line-height: 28px;
    }
  }
  &__link {
    display: inline-block;
    margin-top: 20px;
    position: relative;
    transform: translateX(-50%);
    left: 50%;
    img {
      max-width: 165px;
      @include screen(laptop) {
        max-width: 100%;
      }
    }
    @include screen(laptop) {
      margin-top: 80px;
    }
  }
  &__image {
    grid-column: span 1 / span 1;
    display: flex;
    position: relative;
    img {
      max-width: 245px;
      margin: 0 auto;
      @include screen(tablet) {
        position: absolute;
        bottom: 0;
        transform: translateX(50%);
        right: 50%;
      }
      @include screen(laptop) {
        max-width: 100%;
      }
    }
  }
}