.rates {
  background-color: $black;
  &__wrapper {
    padding: 40px 0;
    @include screen(tablet) {
      padding: 60px 0;
    }
    @include screen(desktop) {
      padding: 80px 0;
    }
  }
  &__title {
    color: $white;
  }
  &__items {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    gap: 20px;
    @include screen(tablet) {
      align-items: center;
      gap: 0;
      flex-direction: row;
    }
  }
  &__content {
    z-index: 2;
    padding: 30px 20px 0;
    position: relative;
    transition: opacity $transition-slow;
    @include screen(tablet) {
      padding: 34px 0 0;
      margin: 0 40px;
      opacity: 0;
      min-width: 321px;
    }
    @include screen(laptop) {
      min-width: 331px;
    }
  }
  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    img {
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
  }
  &__item {
    margin-left: -1px;
    border: 1px solid $white;
    border-radius: 12px;
    overflow: hidden;
    min-width: 70px;
    position: relative;
    width: 100%;
    @include screen(tablet) {
      min-height: 429px;
      max-height: 429px;
      display: flex;
      width: auto;
    }
    @include screen(laptop) {
      min-width: 98px;
    }
    &-blue {
      .rates__aside {
        background-color: #08A6F6;
      }
      .rates__button {
        color: #3498FC;
      }
    }
    &-silver {
      .rates__aside {
        background-color: #999FA7;
      }
      .rates__button {
        color: #969CA4;
      }
    }
    &-gold {
      .rates__aside {
        background-color: #D3A74B;
      }
      .rates__button {
        color: #C68E28;
      }
    }
    &-platinum {
      .rates__aside {
        background-color: #75818A;
      }
      .rates__button {
        color: #828B93;
      }
    }
    &-black {
      .rates__aside {
        background-color: $black;
      }
      .rates__button {
        color: $white;
        background-color: $black;
      }
      .rates__text {
        p {
          color: $black;
        }
        strong {
          color: $black;
        }
      }
    }
    @include screen(tablet) {
      &:hover {
        .rates__card {
          transform: translateX(-50%) rotate(0deg);
          width: 306px;
          left: 265px;
          bottom: -80px;
          @include screen(laptop) {
            left: 324px;
          }
        }
        .rates__block {
          width: 390px;
          @include screen(laptop) {
            width: 400px;
          }
        }
        .rates__content {
          opacity: 1;
        }
        .rates__button {
          opacity: 1;
        }
      }
    }
  }
  &__block {
    z-index: 2;
    position: relative;
    margin-top: -10px;
    transition: $transition-slow;
    @include screen(tablet) {
      width: 0;
      margin-top: 0;
      margin-left: -10px;
    }
  }
  &__aside {
    display: flex;
    justify-content: space-between;
    border-radius: 12px;
    z-index: 3;
    padding: 24px;
    cursor: pointer;
    position: relative;
    @include screen(laptop) {
      padding: 40px 43.5px;
    }
    h3 {
      color: $white;
      font-size: 18px;
      line-height: 21px;
      font-weight: 500;
      letter-spacing: 5px;
      @include screen(tablet) {
        writing-mode: vertical-rl;
        text-align: right;
        transform: scale(-1, -1);
      }
      @include screen(laptop) {
        line-height: 38px;
        font-size: 32px;
      }
    }
  }
  &__button {
    background-color: $white;
    border-radius: 6px;
    margin: 16px 20px;
    font-size: 16px;
    width: calc(100% - 40px);
    line-height: 19px;
    transition: opacity $transition-slow;
    text-align: center;
    padding: 10px;
    z-index: 2;
    position: relative;
    @include screen(tablet) {
      opacity: 0;
      width: auto;
      min-width: 321px;
      margin: 16px 40px 0;
    }
    @include screen(laptop) {
      min-width: 331px;
    }
  }
  &__card {
    z-index: 2;
    position: absolute;
    cursor: default;
    transition: $transition-slow;
    bottom: 5px;
    width: 90px;
    right: 8px;
    img {
      width: 100%;
    }
    @include screen(tablet) {
      bottom: 0;
      width: 170px;
      left: 0;
      transform: rotate(60deg);
    }
  }
  &__text {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 12px;
    padding: 16px 0;
    border-bottom: 1px solid $white;
    p {
      font-size: 16px;
      line-height: 19px;
      color: $white;
    }
    strong {
      font-size: 30px;
      font-weight: 600;
      line-height: 36px;
      color: $white;
      display: flex;
      align-items: center;
      svg {
        margin-left: 8px;
        margin-top: 2px;
      }
    }
    &:first-child {
      padding-top: 0;
    }
    &:last-child {
      border-bottom: 0;
      strong {
        font-size: 18px;
        line-height: 21px;
        font-weight: 700;
      }
    }
  }
}