.choose {
  &__wrapper {
    padding: 40px 0;
    @include screen(tablet) {
      padding: 60px 0;
    }
    @include screen(desktop) {
      padding: 80px 0;
    }
  }
  &__text {
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    gap: 16px;
    @include screen(tablet) {
      flex-direction: row;
      gap: 40px;
    }
    @include screen(laptop) {
      display: block;
    }
    p {
      font-size: 14px;
      line-height: 17px;
      max-width: 264px;
      width: 100%;
      @include screen(laptop) {
        max-width: 100%;
        font-size: 20px;
        line-height: 24px;
      }
    }
  }
  &__subtitle {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 12px;
    @include screen(tablet) {
      align-items: center;
      gap: 40px;
      flex-direction: row;
    }
    h3 {
      color: #007DFBBB;
      text-transform: uppercase;
      font-weight: 700;
      font-size: 22px;
      line-height: 26px;
      @include screen(tablet) {
        font-size: 24px;
        line-height: 28px;
      }
    }
    span {
      color: #646464;
      font-size: 12px;
      line-height: 14px;
      @include screen(tablet) {
        font-size: 14px;
        line-height: 16px;
      }
    }
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(1, minmax(0, 1fr));
    gap: 20px;
    @include screen(laptop) {
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }
    @include screen(desktop) {
      gap: 40px;
    }
  }
  &__item {
    border-radius: 12px;
    padding: 24px;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    justify-content: space-between;
    position: relative;
    overflow: hidden;
    @include screen(tablet) {
      padding: 24px 0px 0px 24px;
      min-height: 172px;
    }
    @include screen(laptop) {
      min-height: 476px;
      padding: 50px 30px 30px 50px;
    }
    &-white {
      background-color: $white;
      border: 1px solid $blue-400;
      .choose__image {
        transform-origin: bottom;
        top: -120px;
        right: -90px;
        max-width: 150px;
        transform: rotate(-135deg);
        @include screen(tablet) {
          bottom: -160px;
          max-width: 300px;
          top: auto;
          right: auto;
          left: 50%;
          transform: translateX(-50%);
        }
        @include screen(laptop) {
          max-width: 100%;
          bottom: -200px;
          transform: translateX(0);
          left: -80px;
        }
      }
      @include screen(laptop) {
        &:hover {
          .choose__image {
            transform: scale(1.2);
          }
        }
      }
    }
    &-black {
      border: 1px solid $white;
      background-color: $black;
      .choose__image {
        max-width: 200px;
        right: -70px;
        bottom: -50px;
        transform-origin: right;
        @include screen(tablet) {
          top: -80px;
          bottom: auto;
          right: -100px;
          transform: rotate(-25deg);
        }
        @include screen(laptop) {
          transform: rotate(0);
          top: 55px;
          right: -200px;
          max-width: 100%;
        }
      }
      .choose__button {
        background-color: $white;
        color: $black;
      }
      .choose__subtitle {
        h3 {
          color: $white;
        }
      }
      .choose__text {
        @include screen(laptop) {
          max-width: 400px;
        }
        p {
          color: $white;
        }
      }
      @include screen(laptop) {
        &:hover {
          .choose__image {
            transform: scale(1.2);
          }
        }
      }
    }
  }
  &__content {
    z-index: 2;
    position: relative;
  }
  &__button {
    margin-top: 24px;
    color: $white;
    background-color: $blue-400;
    padding: 12px 48px;
    border-radius: 4px;
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 16px;
    line-height: 19px;
    z-index: 2;
    align-self: center;
    position: relative;
    @include screen(tablet) {
      align-self: flex-end;
      margin-top: 0;
    }
    @include screen(laptop) {
      font-size: 20px;
      line-height: 24px;
      padding: 16px 64px;
    }
  }
  &__image {
    transition: transform $transition-default;
    position: absolute;
  }
}