.footer {
  background-color: $black;
  padding: 55px 0 44px;
  &__wrapper {
    @include screen(tablet) {
      display: grid;
      gap: 40px;
      grid-template-columns: repeat(12, minmax(0, 1fr));
    }
  }
  &__icons {
    grid-column: span 2 / span 2;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    flex-direction: row;
    gap: 24px;
    @include screen(tablet) {
      flex-direction: column;
      justify-content: flex-start;
    }
    @include screen(laptop) {
      gap: 40px;
    }
  }
  &__container {
    margin-top: 32px;
    grid-column: span 10 / span 10;
    @include screen(tablet) {
      margin-top: 0;
    }
  }
  &__logo {
    min-width: 93px;
    max-width: 93px;
    @include screen(tablet) {
      min-width: 68px;
      max-width: 68px;
    }
    @include screen(laptop) {
      min-width: auto;
    }
  }
  &__application {
    min-width: 112px;
    max-width: 112px;
    @include screen(tablet) {
      min-width: 82px;
      max-width: 82px;
    }
    @include screen(laptop) {
      min-width: 171px;
      max-width: 171px;
    }
  }
  &__cookie {
    font-size: 10px;
    line-height: 12px;
    color: #9B9B9B;
    font-weight: 500;
    margin-top: 24px;
    max-width: 740px;
    @include screen(tablet) {
      margin-top: 40px;
    }
  }
  &__copy {
    font-size: 10px;
    line-height: 12px;
    color: $white;
    font-weight: 500;
    margin-top: 24px;
  }
  &__links {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 16px;
    @include screen(tablet) {
      flex-direction: row;
    }
    @include screen(laptop) {
      align-items: center;
      gap: 0;
    }
    &:nth-child(1) {
      gap: 16px;
      @include screen(tablet) {
        gap: 32px;
      }
      @include screen(laptop) {
        gap: 111px;
      }
    }
    &:nth-child(2) {
      gap: 16px;
      margin-top: 16px;
      @include screen(tablet) {
        margin-top: 24px;
        gap: 32px;
      }
      @include screen(laptop) {
        gap: 100px;
        margin-top: 32px;
      }
    }
  }
  &__link {
    color: #9B9B9B;
    transition: color $transition-default;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    @include screen(laptop) {
      font-size: 16px;
      line-height: 19px;
      &:hover {
        color: $white;
      }
    }
  }
}