.hero {
  padding: 148px 0 70px;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  min-height: 100dvh;
  max-height: 100dvh;
  &__bg {
    position: absolute;
    width: 100%;
    bottom: 0;
    &-left {
      left: -80px;
      max-width: 300px;
      @include screen(tablet) {
        left: -40px;
        bottom: 100px;
        max-width: 373px;
      }
      @include screen(laptop) {
        max-width: 500px;
        left: 0;
        bottom: -120px;
      }
      @include screen(desktop) {
        max-width: 600px;
      }
    }
    &-right {
      right: -40px;
      z-index: 3;
      bottom: -100px;
      max-width: 242px;
      @include screen(tablet) {
        right: 0;
        bottom: 100px;
      }
      @include screen(laptop) {
        bottom: -80px;
      }
      @include screen(desktop) {
        max-width: 300px;
      }
    }
  }
  @include screen(tablet) {
    padding: 108px 0 44px;
  }
  @include screen(laptop) {
    padding: 0;
    margin-top: 116px;
    min-height: calc(100dvh - 116px);
    max-height: calc(100dvh - 116px);
  }
  &__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    z-index: 4;
    height: calc(100dvh - 218px);
    position: relative;
    justify-content: space-between;
    @include screen(tablet) {
      max-height: calc(100dvh - 116px);
      justify-content: center;
    }
  }
  &__description {
    margin-top: 20px;
    color: $white;
    font-weight: 500;
    text-align: center;
    font-size: 18px;
    line-height: 23px;
    @include screen(tablet) {
      font-size: 24px;
      line-height: 40px;
    }
    @include screen(laptop) {
      font-size: 24px;
      line-height: 40px;
    }
    @include screen(desktop) {
      font-size: 32px;
      line-height: 40px;
      margin-top: 40px;
    }
  }
  &__button {
    padding: 16px 40px;
    background-color: $blue-400;
    border-radius: 4px;
    color: $white;
    font-size: 20px;
    line-height: 24px;
    @include screen(tablet) {
      margin-top: 60px;
    }
    @include screen(laptop) {
      transition: background-color $transition-default;
      &:hover {
        background-color: $blue-300;
      }
    }
    @include screen(desktop) {
      margin-top: 80px;
    }
  }
}