.why {
  &__wrapper {
    padding: 40px 0;
    @include screen(tablet) {
      padding: 60px 0;
    }
    @include screen(desktop) {
      padding: 80px 0;
    }
  }
  &__items {
    display: grid;
    gap: 16px;
    @include screen(tablet) {
      grid-template-columns: repeat(3, minmax(0, 1fr));
      gap: 20px;
    }
    @include screen(laptop) {
      gap: 40px;
    }
  }
  &__item {
    padding: 12px;
    overflow: hidden;
    border-radius: 12px;
    background-color: $black;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    min-height: 186px;
    @include screen(tablet) {
      min-height: 114px;
    }
    @include screen(laptop) {
      min-height: 227px;
      padding: 30px;
    }
    span {
      color: $white;
      font-size: 14px;
      line-height: 16px;
      padding-right: 50px;
      @include screen(laptop) {
        padding: 0;
        font-size: 24px;
        line-height: 28px;
        transition: color $transition-default;
      }
    }
    &__pc {
      display: none;
      @include screen(tablet) {
        display: flex;
      }
      span {
        font-weight: 500;
        font-size: 14px;
        line-height: 16px;
        @include screen(laptop) {
          font-size: 24px;
          line-height: 28px;
          transition: color $transition-default;
        }
      }
    }
    &__mob {
      display: flex;
      flex-direction: column;
      gap: 8px;
      @include screen(tablet) {
        display: none;
      }
      span {
        font-size: 14px;
        font-weight: 500;
        line-height: 16px;
      }
    }
    &-hidden {
      display: none;
      @include screen(tablet) {
        display: flex;
      }
    }
    @include screen(laptop) {
      transition: background-color $transition-default;
      &:hover {
        background-color: $white;
        span {
          color: $black;
        }
        p {
          color: $black;
          strong {
            color: $black;
          }
        }
        img {
          transform: scale(1.1);
        }
      }
    }
    img {
      position: absolute;
      right: -20px;
      bottom: 0;
      transform-origin: right bottom;
      max-width: 100px;
      @include screen(tablet) {
        max-width: 90px;
      }
      @include screen(laptop) {
        max-width: 100%;
        transition: transform $transition-default;
      }
    }
    p {
      color: $white;
      font-size: 24px;
      line-height: 28px;
      font-weight: 500;
      @include screen(laptop) {
        transition: color $transition-default;
      }
      strong {
        font-size: 32px;
        line-height: 38px;
        font-weight: 500;
        margin-left: 12px;
        @include screen(laptop) {
          font-size: 48px;
          font-weight: 700;
          line-height: 58px;
          transition: color $transition-default;
        }
      }
    }
  }
}