.header {
  position: fixed;
  top: 0;
  left: 0;
  backdrop-filter: blur(10px);
  width: 100%;
  z-index: 100;
  @include screen(laptop) {
    background-color: rgba($white, 0.77);
  }
  &__wrapper {
    display: grid;
    padding: 24px 0;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    @include screen(tablet) {
      padding: 40px 0;
    }
    @include screen(laptop) {
      grid-template-columns: repeat(3, minmax(0, 1fr));
    }
  }
  &__nav {
    display: none;
    align-items: center;
    gap: 40px;
    &.active {
      display: flex;
    }
    @include screen(laptop) {
      display: flex;
    }
  }
  &__link {
    color: $gray-500;
    font-size: 20px;
    line-height: 24px;
    @include screen(laptop) {
      transition: color $transition-default;
      &:hover {
        color: $blue-300;
      }
    }
  }
  &__icon {
    transition: transform $transition-default;
  }
  &__right {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    gap: 32px;
  }
  &__lang {
    position: relative;
    display: none;
    @include screen(laptop) {
      display: block;
    }
    &:hover {
      .header__list {
        visibility: visible;
        opacity: 1;
        z-index: 10;
      }
      .header__icon {
        transform: rotate(180deg);
      }
    }
    &__text {
      display: flex;
      align-items: center;
      gap: 6px;
    }
    &__flag {
      max-width: 22px;
      width: 100%;
    }
    &__current {
      padding-left: 16px;
      display: flex;
      align-items: center;
      gap: 8px;
      &:hover {
        span {
          color: $blue-500;
        }
      }
      span {
        color: $gray-400;
        font-size: 16px;
        line-height: 19px;
        transition: color $transition-default;
      }
    }
  }
  &__list {
    position: absolute;
    top: 48px;
    background-color: $white;
    border-radius: 6px;
    visibility: hidden;
    opacity: 0;
    z-index: -1;
    display: flex;
    flex-direction: column;
    border: 1px solid rgba($black, 0.1);
    padding: 8px;
    transition: visibility $transition-default, opacity $transition-default, z-index $transition-default;
    &:before {
      content: '';
      position: absolute;
      top: -10px;
      left: 0;
      width: 100%;
      height: 10px;
    }
    a {
      display: flex;
      align-items: center;
      gap: 8px;
      font-size: 16px;
      line-height: 19px;
      padding: 4px 16px;
      border-bottom: rgba($black, 0.2);
      @include screen(laptop) {
        transition: color $transition-default;
        &:hover {
          color: $blue-300;
        }
      }
      svg {
        min-width: 32px;
        min-height: 32px;
      }
    }
  }
  &__button {
    border-radius: 4px;
    padding: 8px 16px;
    display: none;
    @include screen(laptop) {
      display: inline-block;
    }
    span {
      font-size: 16px;
      font-weight: 500;
      line-height: 19px;
      color: $gray-400;
      @include screen(laptop) {
        transition: color $transition-default;
      }
    }
    &-blue {
      background-color: $blue-300;
      span {
        color: $white;
      }
      @include screen(laptop) {
        &:hover {
          background-color: $blue-400;
        }
      }
    }
    @include screen(laptop) {
      padding: 8px 16px;
      transition: background-color $transition-default;
      &:hover {
        background-color: $blue-400;
        span {
          color: $white;
        }
      }
    }
  }
  &__burger {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: 32px;
    height: 32px;
    svg {
      width: 24px;
      height: 16px;
      path,
      rect {
        transition: transform $transition-default, opacity $transition-default;
        fill: $white;
      }
    }
    &.active {
      svg {
        rect {
          fill: $black;
          &:nth-child(1) {
            transform: translateX(3px) rotate(45deg);
          }
          &:nth-child(2) {
            opacity: 0;
          }
        }
        path {
          fill: $black;
          &:nth-child(3) {
            transform: translateX(-7px) translateY(3px) rotate(-45deg);
          }
        }
      }
    }
    @include screen(laptop) {
      display: none;
    }
  }
  &.active {
    background-color: $white;
  }
}

.mobMenu {
  display: none;
  position: fixed;
  width: 100%;
  left: 0;
  padding: 32px;
  top: 84px;
  height: calc(100dvh - 84px);
  background-color: $white;
  &__langs {
    display: flex;
    align-items: center;
    gap: 32px;
    padding-bottom: 32px;
    border-bottom: 1px solid rgba($black, 0.35);
  }
  &__lang {
    font-size: 16px;
    line-height: 19px;
    &.active {
      font-weight: 600;
      border-bottom: 1px solid $black;
    }
  }
  &__links {
    padding: 24px 0;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    gap: 24px;
    border-bottom: 1px solid rgba($black, 0.35);
  }
  &__link {
    font-size: 16px;
    line-height: 19px;
    color: $black;
  }
  &.active {
    display: block;
  }
  .header__button {
    display: block;
    border: 1px solid $black;
    span {
      color: $black;
    }
    &-blue {
    border: 0;
    span {
      color: $white;
    }
    }
  }
  &__buttons {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 12px;
    margin-top: 24px;
  }
}