.reviews {
  background-color: $black;
  position: relative;
  padding-bottom: 32px;
  @include screen(tablet) {
    padding-bottom: 50px;
    min-height: 745px;
  }
  @include screen(laptop) {
    padding-bottom: 0;
    min-height: 749px;
  }
  @include screen(desktop) {
    min-height: 780px;
  }
  &-bg {
    z-index: 2;
    position: absolute;
    width: 100%;
    height: 100%;
    img {
      width: 100%;
      object-fit: cover;
      position: absolute;
      height: 100%;
      top: 0;
      left: 0;
    }
  }
  &__wrapper {
    z-index: 3;
    position: relative;
    width: 100%;
    padding: 40px 0;
    @include screen(tablet) {
      padding: 60px 0;
    }
    @include screen(desktop) {
      padding: 80px 0;
    }
  }
  &__title {
    color: $white;
    margin-bottom: 24px;
    @include screen(tablet) {
      margin-bottom: 32px;
    }
    @include screen(laptop) {
      margin-bottom: 50px;
    }
    @include screen(desktop) {
      margin-bottom: 60px;
    }
    br {
      @include screen(tablet) {
        display: none;
      }
    }
  }
  &__slide {
    border-radius: 12px;
    background-color: #282828;
    padding: 24px 8px;
    @include screen(tablet) {
      padding: 24px;
    }
    @include screen(laptop) {
      padding: 32px;
    }
    @include screen(desktop) {
      padding: 40px;
    }
  }
  .swiper {
    &-wrapper {
      align-items: center;
      @include screen(tablet) {
        align-items: center;
        padding: 50px 0;
      }
    }
    &-navigation {
      position: absolute;
      bottom: 0;
      z-index: 3;
      right: 25%;
      align-items: center;
      display: none;
      gap: 35px;
      transform: translateY(50%);
      @include screen(laptop) {
        display: flex;
      }
    }
    &-button {
      &-prev,
      &-next {
        width: 62px;
        position: relative;
        transition: $transition-default;
        height: 62px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        margin: 0;
        left: 0;
        right: 0;
        top: 0;
        background-color: $white;
        &:before,
        &:after {
          display: none;
        }
        svg {
          width: 22px;
        }
        &:hover {
          background-color: $blue-400;
          svg {
            path {
              fill: $white;
            }
          }
        }
        svg {
          path {
            transition: $transition-default;
          }
        }
      }
    }
    &-pagination {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 8px;
      @include screen(laptop) {
        display: none;
      }
      &-bullet {
        width: 12px;
        height: 12px;
        background-color: transparent;
        opacity: 1;
        transition: $transition-default;
        border: 2px solid $white;
        border-radius: 50%;
        &-active {
          border-color: $blue-400;
          background-color: $blue-400;
        }
      }
    }
    &-slide {
      opacity: 0;
      visibility: hidden;
      padding: 1px;
      transition: $transition-slow;
      border-radius: 12px;
      overflow: hidden;
      background: linear-gradient(112.6deg, #00C0FF 3.14%, rgba(24, 119, 242, 0) 20.72%, rgba(0, 192, 255, 0) 20.73%, rgba(0, 192, 255, 0) 80.49%, #00C0FF 99.57%);
      &-prev {
        @include screen(tablet) {
          transform: translateX(400px);
        }
        @include screen(laptop) {
          transform: translateX(200px);
        }
        @include screen(desktop) {
          transform: translateX(300px);
        }
      }
      &-next {
        @include screen(tablet) {
          margin-left: -400px;
        }
        @include screen(laptop) {
          margin-left: -200px;
        }
        @include screen(desktop) {
          margin-left: -300px;
        }
      }
      &-prev,
      &-next {
        opacity: 1;
        visibility: visible;
      }
      p {
        color: #9B9B9B;
        display: -webkit-box;
        transition: max-height $transition-slow;
        overflow: hidden;
        font-size: 14px;
        line-height: 17px;
        @include screen(tablet) {
          max-height: 100px;
        }
      }
      &-active {
        z-index: 3;
        opacity: 1;
        visibility: visible;
        position: relative;
        @include screen(tablet) {
          transform: scale(1.2);
          background: conic-gradient(from -90deg at 50% 50%, #5F3EF2 -156.74deg, #1877F2 2.93deg, #E04DD1 87.63deg, #5F3EF2 203.26deg, #1877F2 362.93deg);
        }
        p {
          color: $white;
          @include screen(tablet) {
            max-height: 1000px;
          }
        }
      }
    }
  }
  &__footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-top: 1px solid rgba($white, 0.4);
    margin-top: 16px;
    padding: 16px 16px 0;
  }
  &__author {
    display: flex;
    align-items: center;
    gap: 16px;
    img {
      min-width: 32px;
      max-width: 32px;
      object-fit: contain;
      min-height: 32px;
      max-height: 32px;
      @include screen(tablet) {
        min-width: 52px;
        max-width: 52px;
        min-height: 52px;
        max-height: 52px;
      }
    }
    h3 {
      line-height: 100%;
      font-weight: 500;
      font-size: 16px;
      color: $white;
      @include screen(tablet) {
        font-size: 20px;
      }
    }
  }
  &__date {
    display: flex;
    align-items: center;
    gap: 16px;
    img {
      min-width: 32px;
      max-width: 32px;
      min-height: 32px;
      max-height: 32px;
      object-fit: contain;
    }
    span {
      font-size: 10px;
      line-height: 12px;
      color: rgba($white, 0.4);
      @include screen(tablet) {
        line-height: 100%;
        font-size: 13px;
      }
    }
  }
}