.base {
  position: relative;
  background-color: $white;
  &-bg {
    z-index: 2;
    position: absolute;
    width: 100%;
    height: 100%;
    img {
      width: 100%;
      object-fit: cover;
      position: absolute;
      height: 100%;
      top: 0;
      left: 0;
    }
  }
  &__wrapper {
    position: relative;
    width: 100%;
    padding: 40px 0;
    @include screen(tablet) {
      padding: 60px 0;
    }
    @include screen(desktop) {
      padding: 80px 0;
    }
  }
  &__title {
    br {
      @include screen(tablet) {
        display: none;
      }
    }
  }
  &__items {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin: 0 auto;
    justify-content: center;
    gap: 20px;
    max-width: 374px;
    @include screen(laptop) {
      gap: 32px;
      max-width: 864px;
    }
  }
  &__inner {
    justify-content: space-between;
    background-color: $gray-900;
    border-radius: 4px;
    display: flex;
    padding: 14px;
    align-items: center;
    position: relative;
    min-width: 215px;
    max-width: 215px;
    @include screen(tablet) {
      min-width: 177px;
      max-width: 177px;
    }
    @include screen(laptop) {
      max-width: 416px;
      padding: 44px 30px;
      border-radius: 16px;
      min-width: 416px;
    }
  }
  &__content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    &-padding {
      .base__tip {
        padding-left: 34px;
        @include screen(laptop) {
          padding-left: 84px;
        }
      }
    }
  }
  &__tip {
    color: $white;
    font-size: 8px;
    line-height: 10px;
    @include screen(laptop) {
      font-size: 16px;
      line-height: 19px;
    }
  }
  &__subtitle {
    display: flex;
    align-items: center;
    gap: 10px;
    @include screen(laptop) {
      gap: 30px;
    }
    h3 {
      color: $white;
      font-weight: 600;
      font-size: 20px;
      line-height: 24px;
      @include screen(laptop) {
        font-size: 48px;
        line-height: 56px;
      }
    }
    p {
      color: $white;
      font-size: 14px;
      line-height: 16px;
      @include screen(laptop) {
        line-height: 28px;
        font-size: 24px;
      }
    }
  }
  &__image {
    position: absolute;
    right: 10px;
    max-width: 50px;
    @include screen(laptop) {
      right: 30px;
      max-width: 100%;
    }
  }
}